import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routes = [
  {
    path:"/",
    redirect:"/main"
  },
  {
    path:"/main",
    component:()=>import("@/views/Main.vue"),
    redirect:"/main/shouye",
    children:[
      {
        path:"shouye",
        name:"1",
        component:()=>import("@/views/Shouye.vue"),
        redirect:"/main/shouye/tuijian",
        children:[
          {
            path:"tuijian",
            name:"推荐",
            component:()=>import("@/views/Tuijian.vue"),
          },
          {
            path:"shuiguo",
            name:"水果",
            component:()=>import("@/views/Shuiguo.vue"),
          },
          {
            path:"shucai",
            name:"蔬菜",
            component:()=>import("@/views/Shucai.vue"),
          },
        ]
      },
      {
        path:"fenlei",
        name:"2",
        component:()=>import("@/views/Fenlei.vue"),
      },
      {
        path:"mine",
        name:"4",
        component:()=>import("@/views/Mine.vue"),
      },
      
    ]
  },
  {
    path:"/eat",
    name:"3",
    component:()=>import("@/views/Eat.vue"),
  },
  {
    path:"/cart",
    name:"5",
    component:()=>import("@/views/Cart.vue"),
  },
  
  {
    path:"/sms",
    name:"sms",
    component:()=>import("@/views/Sms.vue"),
  },
  {
    path:"/login",
    name:"login",
    component:()=>import("@/views/Login.vue"),
  },
  {
    path:"/register",
    name:"register",
    component:()=>import("@/views/Register.vue"),
  },
  {
    path:"/search",
    name:"search",
    component:()=>import("@/views/Search.vue"),
  },
  {
    path:"/city",
    name:"city",
    component:()=>import("@/views/City.vue"),
  },
  {
    path:"/404",
    name:"404",
    component:()=>import("@/views/404.vue"),
  },
  {
    path:"*",
    redirect:{name:"404"}
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
